import "./App.css";
import React from "react";
import { NftGallery } from "react-nft-gallery";

function App() {
  let wallet = new URLSearchParams(window.location.search).get("wallet");
  return (
    <div
      className="App"
      sx={{ overflow: "hidden", backgroundColor: "white !important" }}
    >
      <form action="/protomynft">
        <label for="wallet">Wallet Address: </label>
        <input type="text" id="wallet" name="wallet" />
        <input type="submit" value="Update" />
      </form>
      <NftGallery
        ownerAddress={wallet ? wallet : "vitalik.eth"}
        openseaApiKey="efee489e792341bd80386e4aa45ee32b"
      />
    </div>
  );
}

export default App;
